<template>
  <article class="square_latest">
    
    <div class="square_latest_entries">
    
      <div
        class="square_latest_entry"
        v-for="(team, key) in teams"
        :key="key"
      >
    
        <div class="square_latest_entry_members">
          
          <div
            v-for="(pkmn, key) in team.teamMembers"
            :key="key"
          >
            <img :src="'/2d/'+pkmn.name+'.png'">
          </div>
    
        </div>
    
        <div class="square_latest_entry_tier" :class="team.tier">
          <p>{{team.tier}}</p>
        </div>
    
        <button class="square_latest_entry_visit" @click="$router.push({path:'/battlepark/'+team.tier+'/'+team._id})">view</button>
    
      </div>
    </div>
  </article>
</template>


<script>
import axios from 'axios'
export default {
  data(){
    return {
      teams: {}
    }
  },
  async created(){
    try {
      const res = await axios.post("/mysquad/shared/latest")
      this.teams = res.data.teams
    } catch(err) {
      alert(err)
    }
  }
};
</script>